:root {
    --gray-000: #f8f9fa;
    --gray-100: #f1f3f5;
    --gray-200: #e9ecef;
    --gray-300: #dee2e6;
    --gray-400: #ced4da;
    --gray-500: #adb5bd;
    --gray-600: #868e96;
    --gray-700: #495057;
    --gray-800: #343a40;
    --gray-900: #212529;
    --dark-poole-001: hsl(200, 3%, 12%);
    --dark-poole-002: hsl(0, 0%, 85%);
    --dark-poole-link-color: rgba(255, 255, 255, 0.75);
    --dark-poole-link-hover: #fff;
    --red: #fa5252;
    --pink: #e64980;
    --grape: #be4bdb;
    --purple: #7950f2;
    --indigo: #4c6ef5;
    --blue: #228be6;
    --cyan: #15aabf;
    --teal: #12b886;
    --green: #40c057;
    --yellow: #fab005;
    --orange: #fd7e14;
    --blue-300: #74c0fc;
    --blue-400: #4dabf7;
    --yellow-100: #fff3bf;
    --body-font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --body-font-size: 20px;
    --body-line-height: 1.5;
    --body-color: var(--gray-700);
    --body-bg: #fff;
    --link-color: var(--blue);
    --link-hover-color: #1c7ed6;
    --heading-color: var(--gray-900);
    --border-color: var(--gray-300);
    --border-radius: 0.25rem;
    --code-font: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    --code-color: var(--grape);
    --code-bg: var(--gray-000);
    --spacer: 1rem;
    --spacer-2: calc(var(--spacer) * 1.5);
    --spacer-3: calc(var(--spacer) * 3);
}
@media (prefers-color-scheme: dark) {
    :root {
        --body-color: var(--gray-300);
        --body-bg: var(--gray-800);
        --heading-color: #fff;
        --link-color: var(--blue-300);
        --link-hover-color: var(--blue-400);
        --border-color: rgba(255, 255, 255, 0.15);
        --code-bg: var(--gray-900);
    }
}
[data-theme="dark-poole"] {
    --body-color: var(--dark-poole-002);
    --body-bg: var(--dark-poole-001);
    --heading-color: var(--dark-poole-002);
    //--link-color: var(--dark-poole-link-color);
    //--link-hover-color: var(--dark-poole-link-hover);
    --border-color: rgba(255, 255, 255, 0.15);
    --code-bg: var(--gray-900);
}
* {
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family: var(--body-font);
    font-size: var(--body-font-size);
    line-height: var(--body-line-height);
    color: var(--body-color);
    background-color: var(--body-bg);
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
}
a {
    color: var(--link-color);
}
a:hover,
a:focus {
    color: var(--link-hover-color);
}
a strong {
    color: inherit;
}
img {
    display: block;
    max-width: 100%;
    margin-bottom: var(--spacer);
    border-radius: var(--border-radius);
}
table {
    margin-bottom: 1rem;
    width: 100%;
    border: 0 solid var(--border-color);
    border-collapse: collapse;
}
td,
th {
    padding: 0.25rem 0.5rem;
    border-color: inherit;
    border-style: solid;
    border-width: 0;
    border-bottom-width: 1px;
}
th {
    text-align: left;
}
thead th {
    border-bottom-color: currentColor;
}
mark {
    padding: 0.15rem;
    background-color: var(--yellow-100);
    border-radius: 0.125rem;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    margin-bottom: 0.5rem;
    font-weight: 600;
    line-height: 1.25;
    color: var(--heading-color);
}
h1 {
    font-size: 2rem;
}
h2 {
    margin-top: 1rem;
    font-size: 1.5rem;
}
h3 {
    margin-top: 1.5rem;
    font-size: 1.25rem;
}
h4,
h5,
h6 {
    margin-top: 1rem;
    font-size: 1rem;
}
p {
    margin-top: 0;
    margin-bottom: 1rem;
}
ul,
ol,
dl {
    margin-top: 0;
    margin-bottom: 1rem;
}
dt {
    font-weight: bold;
}
dd {
    margin-bottom: 0.5rem;
}
hr {
    position: relative;
    margin: var(--spacer-2) 0;
    border: 0;
    border-top: 1px solid var(--border-color);
}
abbr {
    font-size: 85%;
    font-weight: bold;
    color: var(--gray-600);
    text-transform: uppercase;
}
abbr[title] {
    cursor: help;
    border-bottom: 1px dotted var(--border-color);
}
blockquote {
    padding: 0.5rem 1rem;
    margin: 0.8rem 0;
    color: var(--gray-500);
    border-left: 0.25rem solid var(--border-color);
}
blockquote p:last-child {
    margin-bottom: 0;
}
@media (min-width: 30em) {
    blockquote {
        padding-right: 5rem;
        padding-left: 1.25rem;
    }
}
figure {
    margin: 0;
}
a[href^="#fn:"],
a[href^="#fnref:"] {
    display: inline-block;
    margin-left: 0.1rem;
    font-weight: bold;
}
.footnotes {
    margin-top: 2rem;
    font-size: 85%;
}
.lead {
    font-size: 1.25rem;
    font-weight: 300;
}
