code,
pre {
    font-family: var(--code-font);
}
code {
    font-size: 85%;
}
pre {
    display: block;
    margin-top: 0;
    margin-bottom: var(--spacer-3);
    overflow: auto;
}
.highlight {
    padding: var(--spacer);
    margin-bottom: var(--spacer);
    background-color: var(--code-bg);
    border-radius: var(--border-radius);
}
.highlight pre {
    margin-bottom: 0;
}
.highlight .highlight {
    padding: 0;
}
.rouge-table {
    margin-bottom: 0;
    font-size: 100%;
}
.rouge-table,
.rouge-table td,
.rouge-table th {
    border: 0;
}
.rouge-table .gutter {
    vertical-align: top;
    user-select: none;
    opacity: 0.25;
}
.gist .markdown-body {
    padding: 15px !important;
}
.container {
    max-width: 45rem;
    padding-left: var(--spacer-2);
    padding-right: var(--spacer-2);
    margin-left: auto;
    margin-right: auto;
}
footer {
    margin-top: var(--spacer-3);
    margin-bottom: var(--spacer-3);
}
.masthead {
    padding-top: var(--spacer);
    padding-bottom: var(--spacer);
    margin-bottom: var(--spacer-3);
}
.masthead-title {
    margin-bottom: 0;
}
.masthead-title a {
    color: inherit;
    text-decoration: none;
}
.masthead-title small {
    font-weight: 400;
    opacity: 0.5;
}
.nav {
    float: right;
    line-height: 1.25rem;
    white-space: nowrap; /* Prevent text from wrapping */
}
.nav a {
    margin-left: 1rem; /* Adjust the margin between links as needed */
}

.page,
.post {
    margin-bottom: 4em;
}
.page li + li,
.post li + li {
    margin-top: 0.25rem;
}
.page-title,
.post-title {
    color: var(--heading-color);
}
.page-title,
.post-title {
    margin-top: 0;
    margin-bottom: var(--spacer);
}
.post-title a {
    color: inherit;
    text-decoration: none;
}
.post-title a:hover,
.post-title a:focus {
    text-decoration: underline;
}
.post-date {
    display: block;
    margin-top: -0.5rem;
    margin-bottom: var(--spacer);
    color: var(--gray-600);
}
.related {
    padding-top: var(--spacer-2);
    padding-bottom: var(--spacer-2);
    margin-bottom: var(--spacer-2);
    border-top: 1px solid var(--border-color);
    border-bottom: 1px solid var(--border-color);
}
.related-posts {
    padding-left: 0;
    list-style: none;
}
.related-posts h3 {
    margin-top: 0;
}
.related-posts a {
    text-decoration: none;
}
.related-posts a small {
    color: var(--gray-600);
}
.pagination {
    display: flex;
    margin: 0 -1.5rem var(--spacer);
    color: grey;
    text-align: center;
}
.pagination-item {
    display: block;
    padding: var(--spacer);
    text-decoration: none;
    border: solid var(--border-color);
    border-width: 1px 0;
}
.pagination-item:first-child {
    margin-bottom: -1px;
}
a.pagination-item:hover {
    background-color: var(--border-color);
}
@media (min-width: 30em) {
    .pagination {
        margin: var(--spacer-3) 0;
    }
    .pagination-item {
        float: left;
        width: 50%;
        border-width: 1px;
    }
    .pagination-item:first-child {
        margin-bottom: 0;
        border-top-left-radius: var(--border-radius);
        border-bottom-left-radius: var(--border-radius);
    }
    .pagination-item:last-child {
        margin-left: -1px;
        border-top-right-radius: var(--border-radius);
        border-bottom-right-radius: var(--border-radius);
    }
}
.message {
    padding: var(--spacer);
    margin-bottom: var(--spacer);
    color: var(--gray-900);
    background-color: var(--yellow-100);
    border-radius: var(--border-radius);
}
#markdown-toc {
    padding: var(--spacer-2) var(--spacer-3);
    margin-bottom: var(--spacer-2);
    border: solid var(--border-color);
    border-width: 1px 0;
}
#markdown-toc::before {
    display: block;
    margin-left: calc(var(--spacer-3) * -1);
    content: "Contents";
    font-size: 85%;
    font-weight: 500;
}
.colors {
    display: grid;
    grid-template-columns: max-content 1fr;
}
.colors dt {
    width: 3rem;
    height: 3rem;
    border-radius: var(--border-radius);
    box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.15);
}
.colors dd {
    margin-left: var(--spacer);
}

